import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { AuthService } from '@core/auth/auth.service';
import { IVentaGenericaResponse } from '../models/venta-generica';
import { environment } from 'environments/environment';
import { Observable } from 'rxjs';

@Injectable()
export class VentaGenericasService {
    constructor(private http: HttpClient, private _authService: AuthService) { }
    
    /**
     * 
     * @param idventagenerica
     * @returns 
     */
    reporteOrdenServicio(idventagenerica: number): Observable<IVentaGenericaResponse> {
        return this.http.get<IVentaGenericaResponse>(`${environment.ventas.reporte_orden_servicio}` + idventagenerica);
    }
}